@media (prefers-reduced-motion: no-preference) {
  .blink {
    animation: blink infinite 1.5s linear;
    -webkit-animation: blink infinite 1.5s linear;
  }
}

@-webkit-keyframes blink {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes blink {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
